<template>
	<view class="tabBar">
		<view v-for="(item,index) in routeData" :key="item.pagePath" @click="go(item.pagePath)" :class="{active:index === active}">
			<view class="">
				<img :src="index === active?item.selectedIconPath:item.iconPath" alt="">
			</view>
			{{item.text}}
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			active:{
				type:Number,
				default:0
			}
		},
		data() {
			return {
				
				routeData: [{
						"pagePath": "/pages/index/index",
						"iconPath": require("../../../static/home2.png"),
						"selectedIconPath":  require("../../../static/home.png"),
						"text": "首页"
					},
					{
						"pagePath": "/pages/login/login",
						"iconPath": require("../../../static/wd1.png"),
						"selectedIconPath": require("../../../static/wd.png"),
						"text": "个人中心"
					}
				]
			}
		},
		methods:{
			go(path){
				// 可以有返回的标识
				// uni.reLaunch({
				//     url:path
				// })
				uni.navigateTo({
					url:path
				})
				
			}
		}
	}
</script>

<style lang="scss">
	.tabBar {
		height: 100rpx;
		background: #332f2f;
		display: flex;
		z-index: 9999;
		color: #ccc;
		font-size: 24rpx;
		>view {
			flex: 1;
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			image{
				width: 40rpx;
				height: 40rpx;
			}
		}
		.active {
			color: #ffbb00;
		}
	}
</style>
